@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    margin: 25px 40px;
}

*{
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
}
.Header{
    display: flex;
    justify-content: space-between;
}

.Header h1{
    margin: 0;
    font-weight: 600;
}

.Header button{
    color: white;
    background-color: #0060D1;
    font-size: 1.3em;
    font-weight: 600;
    border-radius: 8px;
    padding: 0px 20px;
    border: none;
    cursor: pointer;
}

.Header button:hover{
    background-color: #004fa8;
}
.AddTodoContainer{
    position: absolute;
    width: 100vw;
    height: 100%;
    top: 0;
    left: 0;
    display: grid;
    place-items: center;
    -webkit-backdrop-filter: brightness(0.65);
            backdrop-filter: brightness(0.65);
}

.AddTodo form{
    background-color: #F4F4F4;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: clamp(450px, 45vw, 690px);
    border-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

#title, #desc{
    width: clamp(410px, 40vw, 670px);
    border: none;
    font-size: 18px;
    padding: 12px;
    border-radius: 6px;
}

#desc{
    height: 100px;
    resize: none;
}

#title:focus, #desc:focus{
    outline: none;
    box-shadow: inset 3px 3px 7px rgb(202, 202, 202);
}

.AddTodo h3{
    margin: 0px;
}

#NewTaskHeading{
    text-align: center;
    margin: 0px;
    background-color: #0066FF;
    width: clamp(450px, 45vw, 690px);
    color: white;
    border-radius: 15px 15px 0px 0px;
    padding: 5px 0px;
    font-size: 18px;
    font-weight: 600;
}

.AddTodo .AddTodoButtons{
    margin: 20px 0px;
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    width: clamp(450px, 45vw, 690px);
}

#AddTaskBtn, #CancelBtn{
    border: none;
    font-size: 18px;
    width: 120px;
    padding: 7px 0px;
    border-radius: 9px;
    color: white;
    font-weight: 500;
    cursor: pointer;
    margin-bottom: 12px;
}

#AddTaskBtn{
    background-color: #0066FF;
}

#AddTaskBtn:hover{
    background-color: #0044aa;
}

#CancelBtn{
    background-color: #FF0000;
}

#CancelBtn:hover{
    background-color: #a80000;
}

.AddTodo .FormElements{
    margin: 10px 0px;
    font-size: clamp(1rem, 1vw, 1.8rem);
}
.Todo{
    display: flex;
    justify-content: space-between;
    min-width: 250px;
    max-width: 100%;
    padding: 0px 15px 0px 25px;
    border-radius: 13px;
    margin: 15px 0px;
}

.TodoButtons{
    display: flex;
    align-items:center;
}
.TodoButtons button{
    margin-left: 5px;
    border: none;
    border-radius: 50%;
    aspect-ratio: 1;
    background-color: white;
    cursor: pointer;
    width: 30px;
}

#delete{
    font-weight: bold;
}

#delete:hover{
    background-color: #FF0000;
}
.startHide{
    display: none;
}
#start{
    color: #0060D1;
}

#start:hover{
    color: white;
    background-color: #0060D1;
}

.Heading{
    margin-top: 15px;
    text-align: center;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    font-size: 22px;
}

.TodoDoingDone{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.InnerTodoDivs{
    margin: 0px 10px;
}

.left{
    border-left: 18px solid #FF0000;
    background-color: #FFD4D4;
}

.middle{
    border-left: 18px solid #FFC700;
    background-color: #FFF2C3;
}

.right{
    border-left: 18px solid #4EF200;
    background-color: #D7FFC5;
}

.deleteHide{
    display: none;
}

#postpone{
    color: #0060D1;
}

#postpone:hover{
    color: white;
    background-color: #0060D1;
}

.postponeHide{
    display: none;
}
